<template>
	<div class="login-box">
		<el-row>
			<el-col :span="8">
				<el-input id="username" v-model="username" placeholder="请输入帐号">
					<template slot="prepend">帐号</template>
				</el-input>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="8">
				<el-input id="tel" v-model="tel" placeholder="手机号">
					<template slot="prepend">帐号</template>
				</el-input>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="8">
				<el-input id="password" v-model="password" type="password" placeholder="请输入密码">
					<template slot="prepend">密码</template>
				</el-input>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="8">
				<el-input id="repassword" v-model="rePassword" type="password" placeholder="请再次输入密码">
					<template slot="prepend">验证密码</template>
				</el-input>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="5">
				<el-input id="captcha" v-model="captcha" placeholder="请输入验证码">
					<template slot="prepend">验证码</template>
				</el-input>
			</el-col>
			<el-col :span="3">
				<img :src="src" @click="changeClick" />
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="8">
				<el-button id="register-btn" v-on:click="register" style="width:100%" type="primary">注册</el-button>
			</el-col>
		</el-row>
	</div>
</template>

<script>
	export default {
		name: 'Register',
		data() {
			return {
				captcha:'',
				username:'',
				password:'',
				tel:'',
				src:'http://localhost/captcha',
			}
		},
		methods:{
			register(e){
				if(this.rePassword!==this.password){return}
				let data = this.qs.stringify({
							username:this.username,
							password:this.password,
							tel:this.tel,
							captcha:this.captcha,
				});
				this.axios.post('http://localhost/user/register',data).then(res=>{
					console.log(res.data)
					if(res.data.status==='error'){
						this.src = 'http://localhost/captcha?t=' + new Date();
					}else{//注册成功
//						this.$store.commit('login',this.username)

					}
				}).catch(err=>{
					this.src = 'http://localhost/captcha?t=' + new Date();
					console.log(err)
				})
			},
			changeClick(e){
				this.src = 'http://localhost/captcha?t=' + new Date();
			}
		}
	}
</script>

<style>
	.login-box{
		width: 600px;
		height: 600px;
	}
</style>


